* {
  padding: 0;
  margin: 0;
  -webkit-tap-highlight-color: transparent;
}

html,
body {
  padding: 0 !important;
  margin: 0 !important;
  width: 100%;
  height: 100%;
  background-color: #F6F6F6;
  font-size: 0.186667rem;
  font-family: "kaiti" !important;
  box-sizing: border-box;
  position: relative;
}

ul {
  list-style: none;
}

/* Element样式初始化 */

.el-card__header {
  padding: 0.186667rem 0.266667rem;
}

.v-modal {
  background: rgba(1, 1, 1, 0.15) !important;
}

/* 回复评论样式 */

#replyTextBox {
  width: 100%;
  padding-top: 0.133333rem;
  display: none;
  height: 0;
  transition: height 0.3s;
  overflow-y: hidden;
  position: relative;
}

#replyTextBox #replyText {
  width: 100%;
  display: block;
  margin: 0 auto;
  border: 0.013333rem solid #e74851;
  border-radius: 0.133333rem;
  resize: none;
  outline: none;
  box-sizing: border-box;
  padding: 0.133333rem 0.2rem;
  font-size: 0.186667rem;
  padding-right: 0.866667rem;
  height: 100%;
}

#replyTextBox button {
  position: absolute;
  top: 0.133333rem;
  right: 0;
  width: 0.8rem;
  height: calc(100% - 0.133333rem);
  border-top-right-radius: 0.133333rem;
  border-bottom-right-radius: 0.133333rem;
  padding: 0;
  border: none;
  background-color: #e74851;
  color: white;
  cursor: pointer;
  outline: none;
}

#replyTextBox button.xs-screen {
  font-size: 0.133333rem;
  width: 1.066667rem;
}

/* login */

@keyframes animar_fondo {
  from {
    -webkit-transform: scale(1) translate(0);
    -moz-transform: scale(1) translate(0);
    -ms-transform: scale(1) translate(0);
    -o-transform: scale(1) translate(0);
    transform: scale(1) translate(0);
  }

  to {
    -webkit-transform: scale(1.5) translate(0.666667rem);
    -moz-transform: scale(1.5) translate(0.666667rem);
    -ms-transform: scale(1.5) translate(0.666667rem);
    -o-transform: scale(1.5) translate(0.666667rem);
    transform: scale(1.5) translate(0.666667rem);
  }
}

@keyframes identifier {
  from {
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
  }

  to {
    -webkit-transform: scale(1.5);
    -moz-transform: scale(1.5);
    -ms-transform: scale(1.5);
    -o-transform: scale(1.5);
    transform: scale(1.5);
  }
}

/* 显示隐藏动画 */

.show-box-leave-active,
.show-box-enter-active {
  transition: all 1s;
}

.show-box-leave {
  transform: translateX(0);
  opacity: 1;
}

.show-box-leave-to {
  transform: translateX(-0.4rem);
  opacity: 0;
}

.show-box-enter {
  transform: translateX(0.4rem);
  opacity: 0;
}

.show-box-enter-to {
  transform: translateX(0);
  opacity: 1;
}

.iconfont {
  width: 0.4rem;
  height: 0.4rem;
  vertical-align: -0.066667rem;
  fill: currentColor;
  overflow: hidden;
}

/* 清除浮动 */

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: "";
  clear: both;
  height: 0;
}

/* 盒子动画 */

.child-view {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: calc(100% - 0.8rem);
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}

.child-view.heigth {
  height: auto;
}

.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(0.4rem, 0);
  transform: translate(0.4rem, 0);
}

.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-0.4rem, 0);
  transform: translate(-0.4rem, 0);
}

/* scrollbar 样式 */

.scrollBar::-webkit-scrollbar {
  width: 0.16rem;
  background-color: #F5F5F5;
}

.scrollBar::-webkit-scrollbar-thumb {
  border-radius: 0.133333rem;
  -webkit-box-shadow: inset 0 0 0.08rem rgba(0, 0, 0, 0.3);
  background-color: #555;
}

.scrollBar::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 0.08rem rgba(0, 0, 0, 0.3);
  border-radius: 0.133333rem;
  background-color: #F5F5F5;
}

/* 懒加载动画 */

.spinner-circular {
  width: 1.066667rem;
  height: 1.066667rem;
  position: relative;
  margin: 4rem auto;
}

.spinner-circular .double-bounce1,
.spinner-circular .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #000000;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: bounce 2.0s infinite ease-in-out;
  animation: bounce 2.0s infinite ease-in-out;
}

.spinner-circular .double-bounce2 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}

@-webkit-keyframes bounce {
  0%, 100% {
    -webkit-transform: scale(0);
  }

  50% {
    -webkit-transform: scale(1);
  }
}

@keyframes bounce {
  0%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }

  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}